import React, {useState} from 'react';
import axios from 'axios';
import ReCAPTCHA from "react-google-recaptcha";
import {Box, Button, Card, Container, TextField, Typography} from "@mui/material";

const contactFormUrl = 'https://s1ncys8urb.execute-api.us-east-1.amazonaws.com/prod/contactform';

const ContactForm = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [reason, setReason] = useState('');
    const [message, setMessage] = useState('');
    function onChange(value) {
        console.log("Captcha value:", value);
    }
    const submitHandler = (event) => {
        event.preventDefault();
        if (name.trim() === '' || email.trim() === '' || reason.trim() === '') {
            setMessage('All form fields required');
            return;
        }
        const requestConfig = {
            headers: {
                "x-api-key": "yyTR72wGng2P1f1CshGsU114yikgPFIb1XWr4bCm"
            }
        }
        const min = Math.ceil(1);
        const max = Math.floor(100000);
        const num = Math.floor(Math.random() * (max-min) + min);
        const id = '' + num;
        const requestBody = {
            "operation": "create",
            "tableName": "ContactFormTable",
            "payload": {
                "Item": {
                    "Id": id,
                    name: name,
                    email: email,
                    reason: reason
                }
            }
        }
        axios.post(contactFormUrl, requestBody, requestConfig).then(response => {
            setMessage('Contact Form Submitted!');
        }).catch(error => {
            // if (error.response.status === 401) {
            //     setMessage(error.response.data.message);
            // } else {
            setMessage('Server is down!');

        })
    }
        return (
            <div style={{
                backgroundImage: `url("https://d3qy1z8dfilwgh.cloudfront.net/chrome.jpeg")`,
                backgroundPosition: 'center',
                height: '100vh',
                backgroundSize: 'cover'}}>
                <form onSubmit={submitHandler}>
                    <br></br>
                    <br></br>
                    <Container maxWidth='sm'>
                        <Card>
                            <Box m={2} pt={3}>
                    <Typography variant='h4' align='center' color='Text-primary' gutterBottom>Contact Form</Typography>
                        <Typography variant='h8' align='center' color='Text-primary' gutterBottom>Tell us why you'd like to hear from us!</Typography>

                        <TextField   margin='normal'
                        label='Name'
                        placeholder='Enter your full name'
                        name='name'
                        variant='outlined'
                        required
                        value={name}
                                  onChange={event => setName(event.target.value)}
                        fullWidth
                        autoFocus/>
                        <TextField   margin='normal'
                                     label='Email'
                                     placeholder='Enter your email'
                                     name='email'
                                     variant='outlined'
                                     required
                                     value={email}
                                     onChange={event => setEmail(event.target.value)}
                                     fullWidth
                                     autoFocus/>
                        <TextField   margin='normal'
                                     label='Reason'
                                     placeholder='Enter the reason for contacting us'
                                     name='reason'
                                     variant='outlined'
                                     required
                                     value={reason}
                                     onChange={event => setReason(event.target.value)}
                                     fullWidth
                                     autoFocus/>
                        <Button variant='contained' color='primary' type='submit' fullWidth sx={{ mt: 3, mb: 2 }}>
                            Submit
                        </Button>
                            <ReCAPTCHA sitekey = "6LdWgX0fAAAAAEdSq4PHgISIW7xz3wp9pGApytiM"
                                       onChange={onChange}/>
                            </Box>
                        </Card>
                    </Container>
                </form>
                {message && <p className="message">{message}</p>}

            </div>
        )
}
export default ContactForm;