import React from 'react';
import {AppBar, Toolbar, IconButton, Badge, MenuItem, Menu, Typography, Button, Box}
    from '@mui/material';
import {ShoppingCart} from '@mui/icons-material';
import logo from '../../assets/petroelectronics.png';
import {Link, useLocation} from 'react-router-dom';
import makeStyles from './navbarStyles';
import cart from "../Cart/Cart";
import { Link as RouterLink } from 'react-router-dom';

const Navbar = ({totalItems}) => {
    const classes = './navbarStyles.js';
    const location = useLocation();

    return (

    <div>
        <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static" className={classes.appBar} color = "inherit">
    <Toolbar>
        <Typography component={Link} to = "/" variant = "h6" className = {classes.title} color="inherit">
            <img src={logo} alt = "PetroElectronics.js" height = "25px" className = {classes.image} />
            Petro Electronics
        </Typography>
        <div className={classes.grow}/>
        {location.pathname === '/' && (
        <div className={classes.button}>
            <IconButton component = {Link} to = "/cart" aria-label="Show cart items" color="inherit">
                {cart !== null ?  <Badge badgeContent ={totalItems} color="secondary">
                            <ShoppingCart/>
                        </Badge> : ''}
            </IconButton>
        </div> )}
        <Button style = {{ position: 'absolute', right :3}} component={RouterLink} to='/contactForm' color='secondary'>
            Contact Us
        </Button>
    </Toolbar>
</AppBar>
        </Box>
    </div>
)
}

export default Navbar